import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import ProfilImageWithDefault from "./ProfilImageWithDefault";
import { useTranslation } from "react-i18next";
import Input from "./Input";
import { deleteUser, updateUser } from "../api/apiCalls";
import { useApiProgress } from "../shared/ApiProgress";
import ButtonWithProgress from "./ButtonWithProgress";
import {
  loginSuccess,
  logoutSuccess,
  updateSuccess,
} from "../redux/authActions";
import Modal from "./Modal";
import { getUserPath } from "../api/apiCallsPath";

const ProfileCard = (props) => {
  const [inEditMode, setInEditMode] = useState(false);

  const { username: loggedInUsername } = useSelector((store) => ({
    username: store.username,
  }));

  const [updatedDisplayName, setUpdatedDisplayName] = useState();
  const routeParams = useParams();
  const pathUsername = routeParams.username;
  const [user, setUser] = useState({});
  const [editable, setEditable] = useState(false);
  const [newImage, setNewImage] = useState();
  const [validationErrors, setValidationErrors] = useState({});
  const [modalVisible, setModalVisible] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();

  //edit edilirken ismi guncelledik
  useEffect(() => {
    setUser(props.user);
  }, [props.user]);

  useEffect(() => {
    setEditable(pathUsername === loggedInUsername);
  }, [pathUsername, loggedInUsername]);

  useEffect(() => {
    setValidationErrors((previousValidationErrors) => ({
      ...previousValidationErrors,
      displayName: undefined,
    }));
  }, [updatedDisplayName]);

  useEffect(() => {
    setValidationErrors((previousValidationErrors) => ({
      ...previousValidationErrors,
      image: undefined,
    }));
  }, [newImage]);

  // const { user } = props;
  const { username, displayName, image } = user;

  const pendingApiCallDeleteUser = useApiProgress(
    "delete",
    getUserPath(username),
    true
  );

  const { t } = useTranslation();

  // Edit mode degistiginde Inputtaki Display Name ismini degistirmesini sagladik
  useEffect(() => {
    if (!inEditMode) {
      setUpdatedDisplayName(false);
      setNewImage(undefined);
    } else {
      setUpdatedDisplayName(displayName);
    }
  }, [inEditMode, displayName]);

  const onClickSave = async () => {
    let image;
    if (newImage) {
      image = newImage.split(",")[1];
    }
    // ['data:image/jpeg;base64,/9j/4AAQSkZJRgA'];  Bize virgulden sonrasi lazim
    // backende orayi ayristirip gonderelim oda 1. indexi oluyor zaten
    const body = {
      displayName: updatedDisplayName,
      image: image,
    };
    try {
      const response = await updateUser(username, body);
      setInEditMode(false);
      setUser(response.data);
      dispatch(updateSuccess(response.data));
    } catch (error) {
      setValidationErrors(error.response.data.validationErrors);
    }
  };

  const onChangeFile = (event) => {
    if (event.target.files.length < 1) {
      return;
    }
    const file = event.target.files[0];
    const fileReader = new FileReader();
    fileReader.onloadend = () => {
      setNewImage(fileReader.result);
    };
    fileReader.readAsDataURL(file);
  };

  const onClickCancel = () => {
    setModalVisible(false);
  };
  const onClickDeleteUser = async () => {
    await deleteUser(username);
    setModalVisible(false);
    dispatch(logoutSuccess());
    history.push("/");
  };

  const pendingApiCall = useApiProgress("put", "/api/1.0/users" + username);

  const { displayName: displayNameError, image: imageError } = validationErrors;

  return (
    <div className="card text-center">
      <div className="card-header">
        <ProfilImageWithDefault
          className="rounded-circle shadow"
          width="200"
          height="200"
          alt={`${username} profile`}
          image={image}
          tempimage={newImage}
        />
      </div>
      <div className="card-body">
        {!inEditMode && (
          <>
            <h3>
              {displayName}@{username}
            </h3>
            {editable && (
              <>
                <button
                  className="btn btn-success d-inline-flex"
                  onClick={() => {
                    setInEditMode(true);
                  }}
                >
                  <i className="material-icons">edit</i>
                  {t("Edit")}
                </button>
                <div className="pt-2">
                  <button
                    className="btn btn-danger d-inline-flex"
                    onClick={() => setModalVisible(true)}
                  >
                    <span class="material-icons">directions_run</span>
                    {t("Delete My Account")}
                  </button>
                </div>
              </>
            )}
          </>
        )}
        {inEditMode && (
          <div>
            <Input
              label="Change Display Name"
              defaultValue={displayName}
              onChange={(event) => {
                setUpdatedDisplayName(event.target.value);
              }}
              error={displayNameError}
            />
            <Input
              type="file"
              onChange={onChangeFile}
              error={imageError}
            ></Input>
            <div>
              <ButtonWithProgress
                className="btn btn-success d-inline-flex mt-2"
                onClick={onClickSave}
                disabled={pendingApiCall}
                pendingApiCall={pendingApiCall}
                text={
                  <>
                    <i className="material-icons">save</i>
                    {t("Save")}
                  </>
                }
              ></ButtonWithProgress>
              <button
                className="btn btn-light d-inline-flex ml-3"
                onClick={() => {
                  setInEditMode(false);
                }}
                disabled={pendingApiCall}
              >
                <i className="material-icons">close</i>
                {t("Cancel")}
              </button>
            </div>
          </div>
        )}
      </div>
      <Modal
        visible={modalVisible}
        onClickCancel={onClickCancel}
        onClickOk={onClickDeleteUser}
        titleMessage={t("Delete My Account")}
        okButton={t("Delete My Account")}
        message={
          <div className="text-center">
            <div>
              <strong style={{ fontSize: "20px" }}>
                {t("Are you sure to delete your account?")}
              </strong>
            </div>
            <div className="mt-2" style={{ fontSize: "18px" }}>
              <span>
                {t("Your user name is:")} {username}
              </span>
            </div>
          </div>
        }
        pendingApiCall={pendingApiCallDeleteUser}
      />
    </div>
  );
};

export default ProfileCard;
